<template>
  <div class="index-centent">
    <div class="top-box">
      <dv-border-box-11 title="累 计 数 据" :titleWidth="200">
        <div class="top-items">
          <div class="top-item" v-for="(item, index) in toplist" :key="index">
            <div class="number number-font">
              <animate-number from="0" :to="item.number" duration="2000" :key="item.number"></animate-number>
            </div>
            <div class="name">{{ item.name }}</div>
          </div>
        </div>
      </dv-border-box-11>
    </div>

    <div class="top-map">
      <dv-border-box-11 title="地 图 展 示" :titleWidth="200">
        <div id="chartViewMap">

        </div>
      </dv-border-box-11>
    </div>

    <div class="bottom-box">
      <dv-border-box-11 title="消 费 券 数 据" :titleWidth="200">
        <dv-scroll-board :config="configTable1" style="width:97%;height:calc(100% - 125px);padding: 6% 1.5% 0" />
      </dv-border-box-11>
    </div>

  </div>
</template>

<script>
export default {
  name: 'indexCentent',
  props: {
    toplist: {
      type: Array,
      default: function () {
        return []
      }
    },
    mapData: {
      type: Array,
      default: function () {
        return []
      }
    },
    coupon_info_list: {
      type: Array,
      default: function () {
        return []
      }
    },

    spotData: {
      type: Array,
      default: function () {
        return []
      }
    },
  },
  data: function () {
    return {
      bgColor: {
        color: [],
        bgColor: "",
      },
      configTable1: {
        //商家最新核销
        header: ['序号','类型', '消费券名称', '发行数量', '核销数量','发行金额','核销金额'],
        data: [],
        rowNum: 5,
        align: ['center', 'center', 'center', 'center','center', 'center', 'center', 'center'],
        columnWidth: [60,150,250,120,120,120,120],
        headerBGC: "#020e4d",
        oddRowBGC: "#05127c",
        evenRowBGC: "#000e6d"
      },
      screenWidth:document.body.clientWidth,
    }
  },
  watch:{
    screenWidth(newVal){
      this.screenWidth = newVal;
      this.echarts_map();
    }
  },
  mounted: function () {
    window.onresize = () => {
      return (() => {
        this.screenWidth = document.body.clientWidth;
      })();
    };
    this.echarts_map();
    let lists = [];
    this.coupon_info_list.forEach((item,index)=>{
      let list = [index+1,item.title,item.coupon_title,item.total_count,item.writeoff_total,item.faxing_price,item.writeoff_price];
      lists.push(list);
    });
    this.$set(this.configTable1, 'data', lists);
  },
  methods: {
    echarts_map() {
      let areadata = this.mapData;
      var area = [];
      var total = 0;
      areadata = areadata.sort(function (a, b) { return a.value - b.value; });
      for (let i = 0; i < areadata.length; i++) {
        var value = parseInt(areadata[i].value);
        if (total < value) {
          total = value;
        }
        area[i] = areadata[i].name;
      }
      if(this.screenWidth < 750){
        areadata = [];
      }
      var echarts = require('echarts');
      let that = this;
      let areavalue = this.setCeil(total);
      this.$axios.get(`${this.$urli}/610800_full.json`)
          .then(function (chinaJson) {
            chinaJson = chinaJson.data;
            echarts.registerMap('Yulin', chinaJson);
            var mapchart = echarts.init(document.getElementById('chartViewMap'));
            var mapoption = {
              tooltip: {
                show: true,
                trigger: 'item',
                formatter: function (params) {
                  if (params.seriesName != "") {
                    return `${params.name}:<br/>核销数：${params.data.value}`;
                  }
                },
              },
              title: [{
                left: 'center',
                textStyle: {
                  color: '#fff'
                }
              }],
              visualMap: {
                min: 0,
                max: areavalue,
                left: '90%',
                bottom: 10,
                text: ['高', '低'], // 文本，默认为数值文本
                calculable: true,
                color: ['#00169d', '#006be4', '#3eabff'],
                textStyle: {
                  color: '#fff'
                },
                show: false,
                formatter: function (value) { //标签的格式化工具。
                  return value; // 范围标签显示内容。
                }
              },
              grid: {
                left: 80,
                top: 100,
                bottom: '20%',
                width: '20%'
              },

              xAxis: {
                type: 'value',
                scale: true,
                show: false, //是否显示 x 轴
                position: 'top',
                splitLine: {
                  show: false
                },
                axisLine: {//坐标 轴线
                  show: true,
                  lineStyle: {
                    color: 'rgba(255,255,255,0.8)',
                  }
                },
                axisTick: {//坐标轴刻度相关设置
                  show: true,
                  lineStyle: {
                    color: '#ddd'
                  }
                },
                axisLabel: {
                  fontSize: 10,
                  margin: 5,
                  rotate: 45,
                  textStyle: {
                    color: '#ddd'
                  },
                  formatter: function (value) { //标签的格式化工具。
                    return value; // 范围标签显示内容。
                  }
                },
              },
              yAxis: {
                type: 'category',
                show: that.screenWidth < 750 ? false : true,
                //name: 'TOP 20',
                nameGap: 10,
                // inverse: true, //是否反向坐标轴
                axisLine: {
                  show: true,
                  lineStyle: {
                    color: '#ccc'
                  }
                },
                axisTick: {
                  show: false,
                  lineStyle: {
                    color: '#ccc'
                  }
                },
                axisLabel: {
                  interval: 0,
                  textStyle: {
                    color: '#ddd'
                  }
                },
                data: area
              },
              geo: {
                map: 'Yulin',
                roam: false,
                layoutCenter: [ that.screenWidth < 750 ? '50%' : '60%', '50%'], // 地图中心在屏幕中的位置
                layoutSize: that.screenWidth < 750 ? '80%' : '90%', //地图的大小
                label: {
                  normal: {
                    show: true,
                    color: '#ffffff'
                  },
                  emphasis: {
                    color: '#fff'
                  }
                },

                itemStyle: {
                  normal: {
                    areaColor: "rgba(43, 196, 243, 0.42)",
                    borderColor: "rgba(43, 196, 243, 1)",
                    borderWidth: 1
                  },
                  emphasis: {
                    areaColor: "#2B91B7"
                  }
                },
              },
              series: [
                {
                  id: 'bar',
                  type: 'bar',
                  symbol: 'none',
                  itemStyle: {
                    normal: {
                      color: '#ddb926'
                    }
                  },
                  data: areadata // 上方定义的数据
                },

              ]

            };
            // 动态显示tootip
            that.spotData.forEach((item) => {
              item.value.forEach((itm) => {
                const randomHex = () => `#${Math.floor(Math.random() * 0xffffff).toString(16).padEnd(6, "0")}`;
                let obj = {
                  type: 'effectScatter',
                  coordinateSystem: "geo",
                  data: [{
                    name: itm.nickname,
                    value: [itm.longitude, itm.latitude, 2]
                  }],
                  symbolSize: function (val) {
                    return val[2]
                  },
                  encode: {
                    value: 2
                  },
                  rippleEffect: {
                    number: 2,
                    period: 4,
                    scale: 10,
                    brushType: 'stroke',
                    color: randomHex(),
                  },
                  showEffectOn: 'render',

                  effectType: "ripple",
                  emphasis: {
                    scale: false
                  },
                  label: {
                    formatter: '{b}',
                    position: 'right',
                    show: false
                  },
                  itemStyle: {
                    shadowBlur: 1,
                    shadowColor: 'rgba(230, 10, 10, 1)',
                  },
                  zlevel: 3
                }
                mapoption.series.push(obj);
              })
            });
            mapchart.setOption(mapoption);
            window.addEventListener("resize", function () {
              mapchart.resize();
            });
          });

    },
    setCeil(value) {
      if (value < 10) {
        return 10
      } else {
        let num = Number(value.toString().substring(0, 1));
        return (num + 1) * Math.pow(10, (value.toString().length - 1));
      }
    }
  }
}
</script>

