  width: 100%;
  height: 100%;
  align-items: flex-end;
  justify-content: space-between;
  display: flex;
  flex-direction: column;

  .top-box {
    width: 100%;
    height: 16%;

    & .top-items {
      width: 100%;
      height: 100%;
      align-items: flex-end;
      display: flex;
      justify-content: space-between;

      & .top-item {
        width: 25%;
        height: 80%;
        padding-top: 20%;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;

        & .number {
          font-size: 45px;
          color: #ffff12;
          font-weight: bold;
        }

        & .name {
          font-size: 13px;
          color: #fff;
        }
      }
    }
  }

  .top-map {
    width: 100%;
    height: 55%;

    & #chartViewMap {
      width: 100%;
      height: 100%;
    }
  }

  .bottom-box {
    height: 29%;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 750px) {
  .number{
      font-size: 3rem!important;
  }
  .top-box{
     height: 18rem!important;
  }
  .top-items{
    flex-wrap: wrap;
    height: auto !important;
    .top-item{
      width: 50% !important;
      height: 6rem !important;
      padding-top: 4rem !important;
    }
    .top-item:nth-of-type(3){
      padding-top: 1rem !important;
    }
    .top-item:nth-of-type(4){
      padding-top: 1rem !important;
    }
  }
  .top-map{
    height: 30rem !important;
  }
  .bottom-box{
    height: 20rem !important;
    .dv-scroll-board{
      width: 93% !important;
      padding: 4rem 1rem 0 !important;
    }
  }

}
</style>
